.contacts-list {
	margin: 0;
	padding: 0;
	list-style: none;
	row-gap: 0.5rem;
}

@media (width < 728px) {
	.contacts-list {
		display: grid;
		grid-template-columns: 1fr 1fr;
		align-content: space-between;
	}
}

@media (width >=728px) {
	.contacts-list {
		display: flex;
		flex-direction: column;
	}
}

.contacts-list__item {
	display: flex;
	align-items: center;
	column-gap: 0.5rem;
}

.contacts-list__img {
	flex-shrink: 0;
}
